import React, { useState } from 'react'
import { Game } from './Game';

let palabras = [];

export const Option = () => {

    const [option, setOption] = useState('');
    const [showContent, setShowContent] = useState(true);

    const handleClickButtonFacil = () => {
        setOption("facil");
        setShowContent(false);
        palabras = ["Elefante", "Lana", "Kira", "Coli", "León", "Mastodonte", "Delfín", "Perro", "Gato", "Cocodrilo", "Caracol", "Avestruz", "Tigre", "Pelícano", "Colibrí", "Foca", "Tucán", "Oso", "Lince", "Caballo", "Cabra", "Cebra", "Castor", "Mapache", "Koala", "Burro", "Avispa", "Águila", "Cisne", "Cobaya", "Búfalo", "Reno", "Gallina", "Escorpión", "Hormiga", "Halcón", "Gorila", "Gacela", "Jabalí", "Jirafa", "Mamut", "Oveja", "Pulga", "Puma", "Zarigüeya", "Pingüino", "Pulpo"];
        shuffle(palabras);
        let palabrasTemp = [];
        for (let index = 0; index < 6; index++) {
            palabrasTemp.push(palabras[index]);
        }
        palabras = palabrasTemp;
    }

    const handleClickButtonNormal = () => {
        setOption("normal");
        setShowContent(false);
        palabras = ["Elefante", "Lana", "Kira", "Coli", "León", "Mastodonte", "Delfín", "Perro", "Gato", "Cocodrilo", "Caracol", "Avestruz", "Tigre", "Pelícano", "Colibrí", "Foca", "Tucán", "Oso", "Lince", "Caballo", "Cabra", "Cebra", "Castor", "Mapache", "Koala", "Burro", "Avispa", "Águila", "Cisne", "Cobaya", "Búfalo", "Reno", "Gallina", "Escorpión", "Hormiga", "Halcón", "Gorila", "Gacela", "Jabalí", "Jirafa", "Mamut", "Oveja", "Pulga", "Puma", "Zarigüeya", "Pingüino", "Pulpo"];
        shuffle(palabras);
        let palabrasTemp = [];
        for (let index = 0; index < 9; index++) {
            palabrasTemp.push(palabras[index]);
        }
        palabras = palabrasTemp;
    }

    const handleClickButtonDificil = () => {
        setOption("dificil");
        setShowContent(false);
        palabras = ["Cartón", "Caracol", "Casa", "Caramba", "Canción", "Campeón", "Campo", "Calipso", "Calvo", "Cabeza", "Cactus", "Cabra", "Cacatúa", "Caballo", "Caballero", "Cacahuete", "Casino", "Cable", "Cabaña"];
        shuffle(palabras);
        let palabrasTemp = [];
        for (let index = 0; index < 9; index++) {
            palabrasTemp.push(palabras[index]);
        }
        palabras = palabrasTemp;
    }

    function shuffle(array) {
        var currentIndex = array.length, temporaryValue, randomIndex;

        while (0 !== currentIndex) {

            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex -= 1;

            temporaryValue = array[currentIndex];
            array[currentIndex] = array[randomIndex];
            array[randomIndex] = temporaryValue;
        }

        return array;
    }

    return (
        <>
            {option === "facil" ? (
                <Game palabras={palabras} option={option} />
            ) : null}
            { option === "normal" ? (
                <Game palabras={palabras} />
            ) : null}
            { option === "dificil" ? (
                <Game palabras={palabras} option={option} />
            ) : null}
            {
                showContent ? (
                    <div>
                        <div className="container">
                            <h1 className="text-center mt-2">Memory Game</h1>
                            <h6 className="text-center"><i>Creado por Christianinsky</i></h6>
                            <h6 className="text-center mt-4">Elige una opción</h6>
                            <div className="container">
                                <div className="row mt-3">
                                    <div className="col-4 m-auto text-center">
                                        <button onClick={handleClickButtonFacil} className="btn btn-success">Fácil</button>
                                    </div>
                                    <div className="col-4 m-auto text-center">
                                        <button onClick={handleClickButtonNormal} className="btn btn-info">Normal</button>
                                    </div>
                                    <div className="col-4 m-auto text-center">
                                        <button onClick={handleClickButtonDificil} className="btn btn-danger">Difícil</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                ) : null
            }
        </>
    )
}
