import React, { useEffect, useState } from 'react'

export const Time = ({ timeLeft, setTimeLeft }) => {

    const [gameFinish, setGameFinish] = useState(false);

    let timer;

    const myTimer = () => {

        let timesRun = 0;

        timer = setInterval(() => {

            if (timesRun === timeLeft) {
                setGameFinish(true);
                clearInterval(myTimer);
            } else {
                timesRun++;
                setTimeLeft(time => time - 1);
            }

        }, 1000);

    }

    useEffect(() => {
        myTimer();
        return () => {
            clearInterval(myTimer);
        }
    }, [])

    return (
        <>
            {timeLeft}
        </>
    )
}
